import "./Post.css"
export const Post = (props) => {
    return (
        <div>
            <div className= "post__item">
                <div className= "post__img">
                    <img src="data:image/png;base64," alt=""/>
                </div>

                <div className= "post__text">
                    {props.message}
                </div>
                <div className= "post__like">
                    &#10084;
                    {props.likes}
                </div>

            </div>
        </div>


    )
}