import React from "react";

export let labelInput = ({input, meta, ...props}) => {
    console.log(meta)
    const hasError = meta.touched && meta.error;
    return (
        <label className={'login__label label ' + (hasError ? "error" : "")}>
            <div className={'login__label-text label'}>{props.text}</div>
            <input {...props} {...input}/>
            { hasError && <span> { meta.error } </span> }
        </label>
    )
}
